*{
  margin:0;
  background-color:seashell;
  box-sizing:border-box;
  
}
#calc{
  position: absolute;
  margin: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}
button{
  height: 5vw;
  width: 5vw;
  border-radius: 0%;
}
button:hover{
  box-shadow:0 -1px 1px 1px grey;
  background: hotpink;
}

#row5>button:hover{
  background: red;
}
#row5>button{
  width: 20vw;
  
  color:burlywood;
  height: 5vw;
  background: blueviolet;
}
#ans{
  width: 20vw;
  height: 4vw;
  background: brown;
  color:wheat;
  text-align: center;
  font-size: 2vw;
}